.spinner-container {
    height: 440px;
    padding: 2em 0;
    display: -webkit-flex;
    display: flex;
    -webkit-transform: translate(-50%, -50%) scale(.4, .4);
            transform: translate(-50%, -50%) scale(.4, .4);
    position: absolute;
    top: 40%;
    left: 50%;
}

.spinner-holder {
    display: inline-block;
    border-radius: 30px;
    border: 10px solid #FEB628;
    position: relative;
    -webkit-animation-duration: 3.3s;
            animation-duration: 3.3s;
    overflow: hidden;
    margin-right: 20px;
}

.spinner-holder:last-of-type {
  margin-right: 0;
}

.spinner {
    min-height: 100%;
    background: #fff url(/image/slots-design.jpg) repeat-y;
    background-position-x: 45px;
    -webkit-transition: 0.3s background-position-y ease-in-out;
    transition: 0.3s background-position-y ease-in-out;
    padding: 0 120px;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}

.spinner:nth-child(2) {
    margin: 0 20px;
}

.spinner:nth-child(3) {
    background-position-y: 10px;
}

.gradient-fade {
    border-radius: 20px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: -webkit-linear-gradient( top, rgba(64, 64, 64, 1) 0%, rgba(64, 64, 64, 0) 25%, rgba(64, 64, 64, 0) 75%, rgba(64, 64, 64, 1) 100%);
    background: linear-gradient( to bottom, rgba(64, 64, 64, 1) 0%, rgba(64, 64, 64, 0) 25%, rgba(64, 64, 64, 0) 75%, rgba(64, 64, 64, 1) 100%);
}

@media screen and (max-width: 455px) {
    .spinner-container {
        -webkit-transform: translate(-50%, -50%) scale(.35, .35);
                transform: translate(-50%, -50%) scale(.35, .35);
    }
}
@media screen and (min-width: 600px) {
    .spinner-container {
        -webkit-transform: translate(-50%, -50%) scale(.55, .55);
                transform: translate(-50%, -50%) scale(.55, .55);
    }
}

@media screen and (min-width: 1000px) {
    .spinner-wrapper {
        width: 800px;
    }
}

