.base-game-holder {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, .6);
    z-index: 3;
    height: 100%;
}

.base-game-wrapper {
    width: 82%;
    padding: 30px 0;
    background: #cedce7;
    /* Old browsers */
    /* FF3.6-15 */
    background: -webkit-linear-gradient(45deg, #cedce7 0%, #596a72 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(45deg, #cedce7 0%, #596a72 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cedce7', endColorstr='#596a72', GradientType=1);
    /* IE6-9 fallback on horizontal gradient */
    position: fixed;
    left: 50%;
    top: 50%;
    border-radius: 8px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: 0 24px 38px 3px rgba(0, 0, 0, .14), 0 9px 46px 8px rgba(0, 0, 0, .12), 0 11px 15px -7px rgba(0, 0, 0, .2);
}

.base-game-wrapper h2 {
    text-align: center;
    margin: 0;
    text-transform: uppercase;
}

.base-game-wrapper>button {
    position: absolute;
    top: 5px;
    right: 5px;
    background: rgba(0, 0, 0, 0);
    color: #fff;
    border: 1px solid #bababa;
    cursor: pointer;
    height: 30px;
    width: 35px;
    font-size: 20px;
    border-radius: 3px;
}

.base-game-wrapper>button:hover {
    border: 1px solid #fff;
    outline: none;
}

.base-game-wrapper>button:active {
    border: 2px solid #fff;
    outline: none;
}

.base-game {
    display: grid;
    -webkit-justify-content: center;
            justify-content: center;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    border-radius: 4px;
}

.symbol-holder {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 20px;
}

.symbol-holder img {
    height: 80px;
}

.symbol-holder p {
    font-size: 24px;
    margin: 0;
    font-weight: bold;
    color: #fff;
}

@media screen and (max-width: 800px) {
    h1 {
        font-size: 1.5em;
    }
    .spin-btn,
    .bet-max-btn {
        width: 60px;
        height: 60px;
        font-size: 18px;
    }
    .bet-input,
    .balance,
    .win {
        width: 60px;
        height: 40px;
        font-size: 30px;
        line-height: 40px;
    }
    .balance-name,
    .win-name {
        font-size: 16px;
        bottom: -29px;
    }
    .bet-wrapper label {
        font-size: 16px;
    }
}
@media screen and (max-width: 455px) {
    .base-game-wrapper {
        width: 100%;
        height: 80vh;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: space-evenly;
                justify-content: space-evenly;
    }
    .symbol-holder img {
        height: 60px;
    }

    .symbol-holder p {
        font-size: 20px;
    }
}

@media screen and (min-width: 1000px) {
    .base-game-wrapper {
        width: 806px;
    }
}

@media screen and (max-height: 600px) {
    .base-game-wrapper {
        height: 100vh;
        padding: 5px 0;
        border-radius: 0;
        overflow: auto;
    }
}
